import React, { useState, useEffect } from 'react'
import { useQuery, useMutation } from '@apollo/client'
import {ONE_BUILDING_PRIVATE,SET_DONE,DELETE_BUILDING} from './queries'
import {Table,Button} from 'react-bootstrap'
import Swal from 'sweetalert2'
import { useHistory } from 'react-router-dom'
const ShowBuildingPrivate = ({building})=> {
	const history = useHistory()
	const [toggle,setToggle] = useState(building.done)
	const [setDone,doneResult] = useMutation(SET_DONE)
	const [deleteBuilding, deleteResult] = useMutation(DELETE_BUILDING)
	const result = useQuery(ONE_BUILDING_PRIVATE,{variables:{id:building.id}})
	const swalWithBootstrapButtons = Swal.mixin({
		customClass: {
			confirmButton: 'btn btn-danger',
			cancelButton: 'btn btn-success'
		},
		buttonsStyling: false
	})
	const handleToggle = ()=>{
			setDone({variables:{property:"building",id:building.id}})
			setToggle(!toggle)
		}
	if(result.loading){
	  return (<div>Loading</div>)
    }
	const handleDelete = ()=>{
	swalWithBootstrapButtons.fire({
		title: '       ',
		text: "    ",
		icon: 'warning',
			showCancelButton: true,
			confirmButtonText: ',  !',
			cancelButtonText: ',  ',
			reverseButtons: true
			}).then((result) => {
		if (result.isConfirmed) {
			deleteBuilding({variables:{id:building.id}})
			swalWithBootstrapButtons.fire(
			' !',
			'   ',
			'success'
		)
			history.push('/')
		} else if (
		result.dismiss === Swal.DismissReason.cancel
		) {
		swalWithBootstrapButtons.fire(
		  ' ',
		  '   ',
		  'error'
		)
	  }
	})
	}
	const privateBuilding = result.data.allBuildings[0]
	return (
	<div id="private">
	<h2 className="float-right">{" "}</h2>
	<Table className='mb-3' striped bordered hover>
	<tbody>
	<tr>
	<td>{" "}</td>
	<td>{privateBuilding.ownerName}</td>
	</tr>
	<tr>
	<td>{"  "}</td>
	<td>{privateBuilding.ownerPhone}</td>
	</tr>
	<tr>
	<td>{" "}</td>
	<td>{privateBuilding.done? " " : "  "}</td>
	</tr>
	</tbody>
	</Table>
	<Button className="px-4 m-3" variant={toggle ? "primary":"secondary"} onClick={handleToggle}>{toggle ? " ":" "}</Button>
    <Button className="px=4 m-3" variant='danger' onClick={handleDelete}>{" "}</Button>
	</div>
	)
}

export default ShowBuildingPrivate